@import url(https://fonts.googleapis.com/css2?family=Island+Moments&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
:root {
  --primary-color: #ff7e01;
  --secondary-color: #faa935;
  --heading-color: #0b2727;
  --text-color: #6e7074;
  --font-name: "Montserrat", sans-serif;
  --subtitle-font-name: "Island Moments", cursive;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-name);
}

section {
  padding: 50px 0px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--heading-color);
}

a {
  text-decoration: none;
  color: unset;
}
ul {
  list-style: none;
}

.primary__btn {
  background: var(--secondary-color) !important;
  border: none !important;
  border-radius: 50px !important;
  padding: 0.4rem 1.5rem !important;
}

.primary__btn a {
  font-size: 1.1rem;
  text-decoration: none;
  color: #fff;
}
.primary__btn a:hover {
  color: inherit;
}
.secondary__btn {
  background: transparent !important;
  color: #0b2727 !important;
  border: none !important;
  font-weight: 500 !important;
}

.secondary__btn a {
  text-decoration: none;
  font-size: 1.1rem;
  color: var(--heading-color);
}

.secondary__btn a:hover {
  color: inherit;
}

.section__subtitle {
  background: var(--secondary-color);
  font-family: var(--subtitle-font-name);
  width: -webkit-max-content;
  width: max-content;
  padding: 0px 0.5rem;
  padding-right: 1rem;

  border-radius: 50px;
  font-weight: 500;
  font-size: 1.7rem;
  color: var(--heading-color);
}


/* =============== RESPONSIVE ================ */
@media only screen and (max-width:992px) {
   .primary__btn a,
   .secondary__btn a {
      font-size: 0.9rem;
   }

   .primary__btn,
   .secondary__btn,
   .btn-dark {
      padding: 0.2rem 1.1rem !important;
   }

   section {
      padding: 40px 0;
   }
}

@media only screen and (max-width: 768px) {
   .section__subtitle {
      font-size: 1.3rem;
   }
}

:root {
   --primary-color     : #ff7e01;
   --secondary-color   : #faa935;
   --heading-color     : #0b2727;
   --text-color        : #6e7074;
   --font-name         : "Montserrat", sans-serif;
   --subtitle-font-name: "Island Moments", cursive;
}

.header {
   width      : 100%;
   height     : 80px;
   line-height: 70px;
   background-color:#ffffff ;
   margin-bottom: 1.5rem;
}

.logo img {
   width: 30%;
}

.menu {
   margin-bottom: 0;
}

.nav__item a {
   text-decoration: none;
   color          : var(--heading-color);
   font-weight    : 500;
   font-size      : 1.5rem;
}

.nav__item a:hover {
   color: inherit;
}

.nav__item a.active__link {
   color: var(--secondary-color);
}

.mobile__menu {
   font-size: 1.3rem;
   color    : var(--heading-color);
   display  : none;
}

.sticky__header {
   width      : 100%;
   height     : 80px;
   line-height: 40px;
   background : #fff;
   position   : -webkit-sticky;
   position   : sticky;
   top        : 0;
   left       : 0;
   box-shadow : 3px 3px 8px -3px #ddd;
   z-index    : 999;
}

/* =============== RESPONSIVE ================ */
@media only screen and (max-width:992px) {
   .logo img {
      width: 20%;
   }

   .navigation {
      width     : 100%;
      height    : 100%;
      position  : fixed;
      top       : 0;
      left      : 0;
      background: rgba(0, 0, 0, 0.4);
      z-index   : 999;
      display   : none;
   }

   .menu {
      position       : absolute;
      top            : 0;
      right          : 0;
      width          : calc(100% - 75%);
      height         : 100%;
      background     : #fff;
      flex-direction : column;
      line-height    : 0.5rem;
      justify-content: center;
      z-index        : 9999;
   }

   .show__menu {
      display: block;
   }

   .mobile__menu {
      display: block;
   }
}

/* =============== RESPONSIVE ================ */
@media only screen and (max-width: 768px) {
   .logo img {
      width: 45%;
   }

   .menu {
      width: calc(100% - 50%);
   }
}
.thank__you span i {
   font-size: 4rem;
   color    : green;
}

.thank__you h1 {
   font-size: 2.5rem;
   font-family: var(--subtitle-font-name);
}
:root {
   --primary-color     : #ff7e01;
   --secondary-color   : #faa935;
   --heading-color     : #0b2727;
   --text-color        : #6e7074;
   --font-name         : "Montserrat", sans-serif;
   --subtitle-font-name: "Island Moments", cursive;

}

.hero__subtitle img {
   width : 2.3rem;
   height: 2.3rem;
}

.hero__content h1 {
   font-size    : 2.5rem;
   font-weight  : 600;
   margin-top   : 1rem;
   margin-bottom: 2rem;
   color        : var(--heading-color);
}

.hero__content h1 .hightlight {
   color: var(--secondary-color);
}

.hero__content p {
   font-size  : 1rem;
   color      : var(--text-color);
   line-height: 1.4rem;
}

.hero__img-box img,
.hero__img-box video {
   width        : 100%;
   height       : 350px;
   border-radius: 20px;
   border       : 1px solid var(--secondary-color);
   object-fit   : cover;
}

.hero__content {
   padding-top: 3.5rem;
}

.hero__img-box {
   padding-top: 2rem;
}

.services__subtitle {
   font-size  : 2rem;
   font-weight: 500;
   color      : #ee6e6e;
   font-family: var(--subtitle-font-name);
}

.services__title {
   font-size  : 2rem;
   font-weight: 500;
}

.featured__tour-title {
   font-size  : 1.6rem;
   font-weight: 500;
}

/* EXPERIENCE SECTION */
.experience__content h2 {
   font-size  : 2rem;
   font-weight: 500;
   margin-top : 1rem;
}

.experience__content p {
   font-size : 1.2rem;
   color     : var(--text-color);
   margin-top: 1rem;
}

.counter__wrapper {
   margin-top: 1.6rem;
}

.counter__box span {
   width          : 70px;
   height         : 70px;
   margin         : auto;
   display        : flex;
   align-items    : center;
   justify-content: center;
   background     : var(--primary-color);
   color          : #fff;
   font-size      : 1.2rem;
   font-weight    : 600;
   border-radius  : 10px 5px 10px 5px;
}

.counter__box h6 {
   font-size : 0.8rem;
   margin-top: 0.5rem;
   color     : var(--text-color);
}

.experience__img img {
   width: 90%;
}

/* GALLERY SECTION */
.gallery__title {
   font-size    : 1.6rem;
   margin-bottom: 2.5rem;
   margin-top   : 0.8rem;
}

.masonry__img {
   transition: 0.3s;
}

.masonry__img:hover {
   transform: scale(1.1);
}

/* TESTIMONIAL SECTION */
.testimonial__title {
   font-size : 1.6rem;
   margin-top: 0.8rem;
}

/* =============== RESPONSIVE ================ */
@media only screen and (max-width:992px) {
   .hero__content h1 {
      font-size: 2.5rem;
   }

   .hero__img-box {
      display: none;
   }

   .hero__video-box {
      display: block;
   }

   .services__title,
   .featured__tour-title,
   .gallery__title,
   .testimonial__title,
   .experience__content h2 {
      font-size: 2rem;
   }

   .experience__img {
      margin-top: 2rem;
   }
}

@media only screen and (max-width: 576px) {
   .hero__content h1 {
      font-size: 1.8rem;
   }

   .services__title,
   .featured__tour-title,
   .gallery__title,
   .testimonial__title,
   .experience__content h2 {
      font-size: 1.5rem;
   }
}
.search__bar {
   padding      : 0.5rem 1rem;
   border-radius: 50px;
   box-shadow   : rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
   width        : -webkit-max-content;
   width        : max-content;
   margin-top   : 20px;

}

.form__group span {
   font-size: 1.8rem;
   color    : #ee6e6e;
}

.form__group input {
   border     : none;
   font-size  : 0.9rem;
   color      : var(--heading-color);
   font-weight: 500;
}

.form__group input:focus {
   outline: none;
}

.form__group h6 {
   margin-bottom: 0;
}

.form__group-fast {
   border-right: 2px solid #ddd;
}

.search__icon i {
   font-size    : 1.6rem;
   padding      : 0.5rem;
   background   : var(--secondary-color);
   color        : #fff;
   border-radius: 10px 5px 10px 5px;
   cursor       : pointer;
}

/* =============== RESPONSIVE ================ */
@media only screen and (max-width:992px) {
   .search__bar {
      width: 100%;
      border-radius: 2rem;
   }

   .search__bar form {
      flex-wrap: wrap;
   }

   .search__bar form .form__group {
      width: 48%;
   }
}

@media only screen and (max-width:576px) {
   .search__bar form .form__group {
      width: 100%;
   }

   .form__group input {
      font-size: 0.7rem;
   }

   .form__group span i {
      font-size: 1.1rem;
   }

   .form__group h6 {
      font-size: 1rem;
   }
}
.service__item {
   padding      : 1rem;
   border-bottom: 1px solid #faa83586;
   border-right : 1px solid #faa83586;
   border-radius: 0.5rem;
}

.service__img {
   width          : 50px;
   height         : 50px;
   display        : flex;
   align-items    : center;
   justify-content: center;
   border-radius  : 50px;
   background     : var(--secondary-color);
   padding        : 0.5rem;
   margin-bottom  : 1rem;
   line-height    : 50px;
}

.service__img img {
   width: 100%;
}

.service__item h6 {
   font-size  : 1.1rem;
   font-weight: 500;
}

.service__item p {
   font-size: 0.8rem;
   color    : var(--text-color);
}

