.common__section {
   background: linear-gradient(rgba(0,0,0,0.45), rgba(0,0,0,0.45)),
   url(/static/media/tour.4aa39fc9.jpg) no-repeat center center;
   height: 400px;
   display: flex;
   text-align: center;
   align-items: center;
   justify-content: center;
   background-size: cover;
}

.common__section h1 {
   font-size: 2.5rem;
   color: #fff;
}
.tour__content img {
   width        : 100%;
   border-radius: 0.5rem;
   margin-bottom: 1rem;
}

.tour__info,
.tour__reviews {
   border-radius: 0.5rem;
   border       : 1px solid var(--secondary-color);
   padding      : 1.5rem;
}

.tour__info h2 {
   font-size: 1.5rem;
}

.tour__info span {
   display    : flex;
   align-items: center;
   grid-column-gap : 0.5rem;
   column-gap : 0.5rem;
   font-size  : 0.8rem;
   color      : var(--text-color);
}

.tour__info span i {
   color    : var(--secondary-color);
   font-size: 1rem;
}

.tour__extra-details {
   display      : flex;
   align-items  : center;
   grid-column-gap   : 2.5rem;
   column-gap   : 2.5rem;
   margin-top   : 1rem;
   margin-bottom: 2.5rem;
}

.tour__info h5 {
   margin-bottom: 1rem;
   font-size    : 1.3rem;
}

.tour__info p {
   line-height: 30px;
   font-size  : 1rem;
   color      : var(--text-color);
}

.tour__reviews h4{
   margin-bottom: 1.5rem;
}

.rating__group span {
   font-size: 1rem;
   display: flex;
   align-items: center;
   color: var(--secondary-color);
   cursor: pointer;
}

.review__input {
   display: flex;
   align-items: center;
   justify-content: space-between;
   width: 100%;
   padding: 0.5rem;
   border-radius: 2rem;
   border: 1px solid var(--secondary-color);
}

.review__input input {
   width: 100%;
   padding: 0.5rem 0;
   border: none;
   outline: none;
}

.user__reviews {
   margin-top: 2.5rem;
}

.review__item {
   display: flex;
   align-items: center;
   grid-column-gap: 1rem;
   column-gap: 1rem;
   margin-bottom: 2rem;
}

.review__item img {
   width: 3.5rem;
   height: 3.5rem;
   border-radius: 50px;
   object-fit: cover;
}

.review__item h5,
.review__item p,
.review__item h6 {
   font-size: 0.8rem;
   margin-bottom: 0;
}

.review__item p,
.review__item h6 {
   color: var(--text-color);
}

.review__item  span {
   font-weight: 500;
}

.review__item  span i {
   font-size: 1rem;
   color: var(--secondary-color);
}


/* =============== RESPONSIVE ================ */
@media only screen and (max-width: 768px) {
   .tour__extra-details {
      flex-wrap: wrap;
      grid-row-gap: 1rem;
      row-gap: 1rem;
   }

   .tour__extra-details span {
      font-size: 0.9rem;
   }

   .tour__extra-details span i {
      font-size: 1rem;
   }
}  
.booking {
   padding      : 2rem;
   border-radius: 0.5rem;
   border       : 1px solid var(--secondary-color);
   position     : -webkit-sticky;
   position     : sticky;
   top          : 80px;
}

.booking__top h3 {
   font-size  : 1.2rem;
   font-weight: 700;
}

.booking__top h3 span {
   font-size  : 1rem;
   font-weight: 400;
   color      : var(--text-color);
}

.booking__top span {
   font-size  : 1rem;
   font-weight: 600;
   color      : var(--text-color);
}

.booking__top span i {
   color: var(--secondary-color);
}

.booking__top {
   padding-bottom: 1rem;
   border-bottom : 1px solid rgb(229, 231, 235);
}

.booking__form {
   padding-top: 1rem;
}

.booking__form h5 {
   margin-bottom: 1rem;
}

.booking__form input {
   width        : 100%;
   padding      : 0.4rem;
   border-radius: 0.5rem;
   color        : var(--heading-color);
   font-size    : 0.7rem;
   border-bottom: 1px solid rgb(229, 231, 235);
   border       : none;
}

.booking__form input:focus {
   outline: none;
}

.booking__info-form {
   padding: 0.2rem;
   border : 1px solid rgb(229, 231, 235);
}

.booking__bottom li {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.booking__bottom li h5,
.booking__bottom li span {
   font-size: 0.7rem;
   font-weight: 500;
   color: var(--text-color);
}

.total h5,
.total span {
   font-size: 1rem !important;
   font-weight: 700 !important;
   color: var(--heading-color) !important;
}

.booking__bottom Button:hover {
   transform: scale(1.1);
   transition: 0.5s;
}
.pagination span {
   width          : 2rem;
   height         : 2rem;
   padding        : 5px;
   border-radius  : 50px;
   display        : flex;
   align-items    : center;
   justify-content: center;
   border         : 1px solid var(--secondary-color);
   color          : var(--heading-color);
   font-size      : 1rem;
   cursor         : pointer;
   font-weight    : 500;
}

.active__page {
   background: var(--secondary-color);
   color     : #fff !important;
}
